.cart_items{
    display: flex;
    margin-top: 8px;
    padding:10px;
}
.cart_item_img{
    object-fit: contain;
    width: 180px;
    height:180px;
}
.cart_items_info{
    padding-left: 20px;
}
.cartItem_title{
    font-size: 17px;
    font-weight: 800;
}
.cartItem_rating{
    display: flex;
}
.cart_items_info > button{
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    margin-top: 10px;
    cursor: pointer;
}